<template>
    <v-container class="grey darken-4" fluid fill-height>
      <v-layout align-center justify-center>
        <v-flex>
          <v-card max-width="450" class="mx-auto pa-5">
            <v-card-title>
              <v-spacer></v-spacer>
              <h2>Mart</h2>
              <v-spacer></v-spacer>
            </v-card-title>
            <v-card-text>
              <v-alert v-show="error" dense text type="error" icon="warning">{{ error }}</v-alert>
              <v-form ref="form">
                <v-text-field
                  outlined
                  label="Email"
                  type="text"
                  required
                  v-model="email"
                ></v-text-field>
                <v-text-field
                  outlined
                  label="Password"
                  required
                  :type="showPassword ? 'text' : 'password'"
                  :append-icon="
										showPassword
											? 'visibility'
											: 'visibility_off'
									"
                  @click:append="showPassword = !showPassword"
                  v-model="password"
                ></v-text-field>
              </v-form>
              <v-progress-linear
                  :active="loading"
                  :indeterminate="loading"
                  rounded
                  color="primary"
              ></v-progress-linear>
            </v-card-text>
            <v-card-actions class="justify-end">
              <v-btn dark color="primary" class="px-8" @click="signin">Sign In</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
</template>

<script>

export default {
  data: () => ({
    error:"",
    loading: false,
    showPassword: false,
    email: undefined,
    password: undefined,
    emailRule: [
      email => !!email || "Email is required",
      email =>
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/.test(
          email
        ) || "Email must be valid"
    ],
    passwordRule: [
      password => !!password || "Password is required",
      password =>
        /(?=.{8,})/.test(password) || "Password must be at least 8 characters"
    ]

  }),
  methods: {
    signin() {
      if (this.$refs.form.validate()) {
        this.$store
          .dispatch('signin', {
            email: this.email,
            password: this.password
          })
          .then(() => {
            this.$router.push("/");
          })
          .catch(err => {
            console.log(err);
            this.error = err;
          });
      }
    }
  }
}
</script>

<style scoped>
#fontSize {
  font-size: 20px;
}
</style>