<template>
  <v-container fill-height fluid grid-list-xl>
    <v-layout justify-center wrap>
      <v-flex xs12 md6>
        <material-card color="general" title="Profile" class="pa-3" v-if="edit">
          <v-row >
            <v-col
              v-for="data in profileInfo"
              :key="data.title"
              :v-bind="data"
              :md="12"
            >
              <v-text-field
                :label="data.title"
                :value="data.data"
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field :label="'Password'"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="edit = !edit">Save</v-btn>
          </v-row>
        </material-card>
        <material-card
          color="general"
          title="Profile"
          class="pa-3"
          v-if="!edit"
        >
          <v-row v-for="data in profileInfo" :key="data.title">
            <v-col :md="4">{{ data.title }}</v-col>
            <v-col :md="8">{{ data.data }}</v-col>
            <v-divider></v-divider>
          </v-row>
          <v-row>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="edit = !edit">Edit</v-btn>
          </v-row>
        </material-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import serviceUser from "../services/user"
export default {
  data: () => ({
    edit: false,
    profileInfo: [
      {
        title: "Name",
        data: "",
      },
      {
        title: "Email",
        data: "",
      },
      {
        title: "Role",
        data: ""
      }
    ],
  }),
  created() {
    this.getCurrentUser();
  },
  methods: {
    getCurrentUser() {
      serviceUser.getCurrentUser()
        .then((resp) => {
          this.profileInfo[0].data = resp.data.data[0].username
          this.profileInfo[1].data = resp.data.data[0].email
          this.profileInfo[2].data = resp.data.data[0].role
          console.log(resp)})
        .catch((err) => console.log(err));
    },
  },
};
</script>