<template>
  <v-row justify="center">
    <v-col cols="11">
      <material-card color="green" :title="$route.name" class="pa-3">
        <v-row>
          <v-btn
            color="primary"
            dark
            class="mb-2"
            text
            @click="clickNewCategory"
            ><v-icon left>mdi-plus-outline</v-icon>Category</v-btn
          >
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-row>
        <v-data-table
          :headers="headers"
          :items="categories"
          :search="search"
          @click:row="clickRowCategory"
        >
        </v-data-table>
      </material-card>
    </v-col>

    <v-dialog v-model="addDialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ $route.name }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="4">
                <v-subheader>Name</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-text-field v-model="newItem.name"></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="addDialog = false">
            Close
          </v-btn>
          <v-btn color="blue darken-1" text @click="clickSave"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import serviceCategory from "../../services/category";

export default {
  data: () => ({
    addDialog: false,
    search: "",
    newItem: {
      name: "",
    },
    headers: [
      {
        text: "# ID",
        align: "start",
        sortable: true,
        value: "id",
      },
      { text: "Name", value: "name" },
    ],
    categories: [],
  }),
  computed: {},
  created() {
    this.getCategory();
  },
  methods: {
    getCategory() {
      serviceCategory.getAllCategory().then((res) => {
        this.categories = res.data.data;
      });
    },
    clickRowCategory(item) {
      console.log(item);
    },
    clickNewCategory() {
      this.addDialog = true;
    },
    clickSave() {
      serviceCategory.postCategory(this.newItem).then((res) => {
        console.log(res)
        this.addDialog = false
      });
      this.getCategory()
    },
  },
};
</script>

