<template>
  <div style="height: 100%">
    <v-sheet height="100%" elevation="1">
      <v-row align="center">
        <v-col class="d-flex" cols="12" sm="8">
          <v-tabs
            v-model="tab"
            next-icon="mdi-arrow-right-bold-box-outline"
            prev-icon="mdi-arrow-left-bold-box-outline"
            show-arrows
          >
            <v-tabs-slider color="yellow"></v-tabs-slider>
            <v-tab v-for="i in categories" :key="i.id" :href="'#' + i.id">
              {{ i.name }}
            </v-tab>
          </v-tabs>
        </v-col>
        <v-col class="d-flex" cols="12" sm="4">
          <v-text-field
            v-model="searchProduct"
            class="mx-2"
            hide-details
            prepend-icon="mdi-magnify"
            single-line
          ></v-text-field>
        </v-col>
      </v-row>

      <v-row justify="center">
        <v-col cols="12" sm="6" md="8">
          <v-tabs-items v-model="tab">
            <v-tab-item :value="tab">
              <v-flex d-flex>
                <v-layout wrap>
                  <v-flex md4 v-for="item in products" :key="item.id">
                    <v-card class="card-container ma-2">
                      <v-img
                        height="250"
                        src="https://cdn.vuetifyjs.com/images/cards/cooking.png"
                      >
                      </v-img>
                      <v-card-title>
                        {{ item.name }}
                      </v-card-title>

                      <v-card-text>
                        Amount: {{ item.amount }}
                        <v-spacer></v-spacer>
                        Quantity: {{ item.quantity }}
                      </v-card-text>

                      <v-card-actions>
                        <v-btn
                          color="deep-purple lighten-2"
                          text
                          @click="addCart(item)"
                        >
                          Add Cart
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-flex>
                </v-layout>
              </v-flex>
            </v-tab-item>
          </v-tabs-items>
        </v-col>
        <v-col cols="6" md="4">
          <h4>Cart</h4>
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">Name</th>
                  <th class="text-right">Qty</th>
                  <th class="text-right">Amount</th>
                  <th class="text-right">Total</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in cart"
                  :key="item.name"
                  @dblclick="clickRowProduct(item)"
                >
                  <td>{{ item.name }}</td>
                  <td class="text-right">
                    <input
                      style="width: 2em; text-align: right"
                      :value="item.qty"
                    />
                  </td>
                  <td class="text-right">{{ item.amount }} $</td>
                  <td class="text-right">{{ item.total }} $</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <v-divider></v-divider>
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th>Subtotal</th>
                  <th class="text-right">{{ subtotal }} $</th>
                </tr>
                <tr>
                  <th>Product Discount</th>
                  <th class="text-right">
                    <input
                      type="number"
                      style="width: 2em; text-align: right"
                      value=""
                    />
                  </th>
                </tr>
                <tr>
                  <th>Total</th>
                  <th class="text-right">{{ total }} $</th>
                </tr>
              </thead>
            </template>
          </v-simple-table>
          <h4>Payment</h4>
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th>Total</th>
                  <th class="text-right">{{ total }} $</th>
                </tr>
                <tr>
                  <th>Paid</th>
                  <th class="text-right">
                    <input
                      v-model="paid"
                      @change="paidChange"
                      style="width: 4em; text-align: right"
                    />
                    $
                  </th>
                </tr>
                <tr>
                  <th>Change</th>
                  <th class="text-right">{{ change }} $</th>
                </tr>
              </thead>
            </template>
          </v-simple-table>
          <v-row>
            <v-spacer></v-spacer>
            <v-btn @click="checkOut" class="mr-5" dark> Check Out </v-btn>
          </v-row>
        </v-col>
      </v-row>
    </v-sheet>
  </div>
</template>

<script>
import serviceCategory from "../../services/category";
import serviceProduct from "../../services/product";
import serviceUser from "../../services/user";
import serviceTranaction from "../../services/transaction";

const _ = require("lodash");
export default {
  name: "Home",
  components: {},
  data: () => ({
    categories: [],
    tab: null,
    products: [],
    searchProduct: "",
    cart: [],
    subtotal: 0,
    total: 0,
    user: "",
    paid: 0,
    change: 0,
  }),
  watch: {
    tab: function () {
      this.products = [];
      this.getProductsByCategory();
    },
    searchProduct: function () {
      this.debouncedProduct();
    },
    cart: function () {
      this.total = 0;
      this.subtotal = 0;
      this.cart.forEach((element) => {
        this.subtotal += element.total;
      });
      this.total = this.subtotal;
    },
  },
  created() {
    this.getCategory();
    this.getCurrentUser();
    this.debouncedProduct = _.debounce(this.getSearchProducts, 500);
  },
  methods: {
    getCategory() {
      serviceCategory
        .getAllCategory()
        .then((resp) => {
          this.categories = resp.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getProductsByCategory() {
      serviceProduct
        .getProductByCategory(this.tab)
        .then((res) => {
          this.products = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getSearchProducts() {
      if (this.searchProduct.length > 0) {
        serviceProduct.getSearchProductName(this.searchProduct).then((resp) => {
          this.products = resp.data.data;
        });
      } else {
        this.getProductsByCategory();
      }
    },
    clickRowProduct(item) {
      const _index = this.cart.indexOf(item);
      if (_index > -1) {
        this.cart.splice(_index, 1);
      }
    },
    addCart(item) {
      if (this.cart.indexOf(item) > -1) {
        this.cart[this.cart.indexOf(item)]["qty"] =
          this.cart[this.cart.indexOf(item)]["qty"] + 1;
        this.cart[this.cart.indexOf(item)]["total"] =
          this.cart[this.cart.indexOf(item)]["qty"] *
          this.cart[this.cart.indexOf(item)]["amount"];
        var x = this.cart;
        this.cart = [];
        this.cart = x;
      } else {
        item["qty"] = 1;
        item["total"] = item["qty"] * item["amount"];
        this.cart.push(item);
      }
    },
    paidChange() {
      this.change = this.paid - this.total;
    },
    getCurrentUser() {
      serviceUser.getCurrentUser().then((res) => {
        this.user = res.data.data[0];
      });
    },
    checkOut() {
      var product = [];
      if (this.paid == 0 || this.total == 0) {
        alert("Enter Paid");
      } else {
        this.cart.forEach(function (e) {
          product.push({
            product_id: e.id,
            product_qty: e.qty,
          });
        });

        var c = {
          user_id: this.user.id,
          total: this.total,
          paid: this.paid,
          paid_change: this.change,
          product: product,
        };
        serviceTranaction.postTransaction(c).then(() => {
          this.$router.go();
        });
      }
    },
  },
};
</script>

