<template>
  <div>
    <v-row justify="center">
      <v-col cols="11">
        <material-card color="red" title="Product Amount">
          <v-data-table
            :headers="headers"
            :items="products"
          ></v-data-table>
        </material-card>
        <material-card color="green" title="Product">
          <v-sheet height="50%" color="rgba(0, 0, 0, 0)">
            <v-sparkline
            :labels="labels"
              :value="value"
              color="rgba(0, 0, 0, .7)"
              height="100"
              padding="24"
              stroke-linecap="round"
              smooth
            >
              <template v-slot:label="item"> {{ item.value }} </template>
            </v-sparkline>
          </v-sheet>
        </material-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import serviceProduct from "../services/product";
export default {
  data: () => ({
    labels:["Coca", "Pepsi", "Beer", "Pen"],
    headers: [
      {
        text: "# ID",
        align: "start",
        sortable: true,
        value: "id",
      },
      { text: "Name", value: "name" },
      { text: "Quantity", value: "quantity" },
      //   { text: 'Date', value: 'date_joined' },
    ],
    value: [423, 446, 675, 510],
    products: [],
  }),
  computed: {},
  created() {
    this.getProduct();
  },
  methods: {
    getProduct() {
      serviceProduct.getAllProduct().then((res) => {
        this.products = res.data.data;
      });
    },
  },
};
</script>