<template>
  <v-row justify="center">
    <v-col cols="11">
      <material-card color="green" :title="$route.name" class="pa-3">
        <v-row>
          <v-btn color="primary" dark class="mb-2" text @click="clickNewUser"
            ><v-icon left>mdi-plus-outline</v-icon>User</v-btn
          >
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-row>
        <v-data-table
          :headers="headers"
          :items="users"
          :search="search"
          @click:row="clickRowUser"
        >
        </v-data-table>
      </material-card>
    </v-col>
    <v-dialog v-model="addDialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ $route.name }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="4">
                <v-subheader>Username</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-text-field v-model="newItem.username"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4">
                <v-subheader>Email</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-text-field v-model="newItem.email"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4">
                <v-subheader>Password</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-text-field v-model="newItem.password"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4">
                <v-subheader>Role</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-select
                  v-model="select"
                  :items="role"
                  item-text="name"
                  item-value="id"
                  persistent-hint
                  return-object
                  single-line
                ></v-select>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="addDialog = false">
            Close
          </v-btn>
          <v-btn color="blue darken-1" text @click="clickSave"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import serviceUser from "../../services/user";

export default {
  data: () => ({
    addDialog: false,
    select:{
      "id": "staff",
      "name": "Staff"    
    },
    role: [{
      "id": "staff",
      "name": "Staff"    
    },{
      "id": "admin",
      "name": "Admin"    
    }],
    search: "",
    headers: [
      {
        text: "# ID",
        align: "start",
        sortable: true,
        value: "id",
      },
      { text: "Name", value: "username" },
      { text: "Email", value: "email" },
      { text: "Role", value: "role" },
      //   { text: 'Date', value: 'date_joined' },
    ],
    users: [],
    newItem: {
      username: "",
      email: "",
      password: "",
      role: ""
    },
  }),
  computed: {},
  created() {
    this.getUsers();
  },
  methods: {
    getUsers() {
      serviceUser.getUsers().then((res) => {
        this.users = res.data.data;
      });
    },
    clickRowUser(item) {
      console.log(item);
    },
    clickNewUser() {
      this.addDialog = true;
    },
    clickSave() {
      this.newItem.role = this.select.id
      serviceUser.postUser(this.newItem).then(res => {
        this.addDialog = false
        this.getUsers()
        console.log(res)
      })
    },
  },
};
</script>

