<template>
  <div>
    <v-app v-if="$route.name !== 'SignIn'">
      <v-navigation-drawer app v-model="drawer" width="280px" dark>
        <v-list>
          <v-list-item>
            <!-- <v-list-item-avatar> -->
              <!-- <v-avatar> -->
              <img width="25%" height="25%" src="../assets/k.png" />
              <!-- </v-avatar> -->
            <!-- </v-list-item-avatar> -->
            <v-list-item-content class="ml-3">
              <v-list-item-title><h3>Mart</h3></v-list-item-title>
              <v-list-item-subtitle>version: 1.0</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
          <v-divider></v-divider>
          <v-list-item
            class="my-3"
            v-for="(link, i) in links"
            :key="i"
            :to="link.to"
          >
            <v-list-item-action>
              <v-icon>{{ link.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-title v-text="link.text"></v-list-item-title>
          </v-list-item>
        </v-list>
        <template v-slot:append>
          <v-list>
            <v-list-item color="primary" to="/profile">
              <v-list-item-avatar>
                <v-avatar size="32">
                  <v-icon>person</v-icon>
                </v-avatar>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>{{ user.username }}</v-list-item-title>
                <v-list-item-subtitle>{{ user.role }}</v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-action @click="signout">
                <v-tooltip top>
                  <template v-slot:activator="{ on }">
                    <v-btn v-on="on" class="ma-0 pa-0" color="error">
                      <v-avatar size="32">
                        <v-icon>mdi-logout </v-icon>
                      </v-avatar>
                    </v-btn>
                  </template>
                  <span>Sign Out</span>
                </v-tooltip>
              </v-list-item-action>
            </v-list-item>
          </v-list>
        </template>
      </v-navigation-drawer>
      <v-app-bar app flat dense dark style="background: #424242">
        <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
        <h4>{{ $route.name }}</h4>
      </v-app-bar>

      <v-main class="grey darken-4">
        <v-fade-transition mode="out-in">
          <router-view />
        </v-fade-transition>
      </v-main>
    </v-app>
    <v-app v-if="$route.name === 'SignIn'">
      <!-- <v-content class="grey darken-4"> -->
      <v-fade-transition mode="out-in">
        <router-view />
      </v-fade-transition>
      <!-- </v-content> -->
    </v-app>
  </div>
</template>

<script>
// import store from '../store'
import serviceUser from '../services/user'
export default {
  data: () => ({
    links: [],
    drawer: false,
    user: {},
  }),
  watch: {
    user: function(){
      if (this.user.role == "admin") {
        this.links = [
          {
            to: "/",
            icon: "mdi-view-dashboard",
            text: "Dashboard",
          },
          {
            to: "/category",
            icon: "mdi-clipboard-outline",
            text: "Category",
          },
          {
            to: "/product",
            icon: "mdi-clipboard-outline",
            text: "Product",
          },
          {
            to: "/sale",
            icon: "mdi-cart-outline",
            text: "Sale",
          },
          {
            to: "/users",
            icon: "mdi-account-group",
            text: "User",
          },
        ];
      } else {
        this.links = [
          {
            to: "/",
            icon: "mdi-view-dashboard",
            text: "Dashboard",
          },
          {
            to: "/sale",
            icon: "mdi-clipboard-outline",
            text: "Sale",
          },
        ];
      }
    }
  },
  created() {
	this.getUser();
  },
  methods: {
    signout() {
      if (confirm("Do you want sign out??"))
        this.$store.dispatch("signout").then(() => {
          this.$router.push("/signin");
        });
    },
    getUser() {
      serviceUser.getCurrentUser().then((resp) => {
        this.user = resp.data.data[0]
      });
    },
  },
};
</script>

<style scoped>
</style>