<template>
  <v-row justify="center">
    <v-col cols="11">
      <material-card color="green" :title="$route.name" class="pa-3">
        <v-row>
          <v-btn color="primary" dark class="mb-2" text @click="clickNewProduct"
            ><v-icon left>mdi-plus-outline</v-icon>Product</v-btn
          >
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-row>
        <v-data-table
          :headers="headers"
          :items="products"
          :search="search"
          @click:row="clickRowProduct"
        >
        </v-data-table>
      </material-card>
    </v-col>

    <v-dialog v-model="addDialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">Product</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="4">
                <v-subheader>Name</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-text-field v-model="newItem.name"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4">
                <v-subheader>Image</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-text-field v-model="newItem.image"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4">
                <v-subheader>Quantity</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-text-field v-model="newItem.quantity"></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4">
                <v-subheader>Category</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-select
                  v-model="select"
                  :items="categories"
                  item-text="name"
                  item-value="id"
                  persistent-hint
                  return-object
                  single-line
                ></v-select>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="4">
                <v-subheader>Amount</v-subheader>
              </v-col>
              <v-col cols="8">
                <v-text-field v-model="newItem.amount"></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="addDialog = false">
            Close
          </v-btn>
          <v-btn color="blue darken-1" text @click="clickSave"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import serviceProduct from "../../services/product";
import serviceCategory from "../../services/category";

export default {
  data: () => ({
    select: "",
    newItem: {
      name: "",
      image: "",
      id_category: "",
      quantity: "",
      amount: "",
    },
    categories: [],
    addDialog: false,
    search: "",
    headers: [
      {
        text: "# ID",
        align: "start",
        sortable: true,
        value: "id",
      },
      { text: "Name", value: "name" },
      { text: "Quantity", value: "quantity" },
      //   { text: 'Date', value: 'date_joined' },
    ],
    products: [],
  }),
  computed: {},
  created() {
    this.getProduct();
  },
  methods: {
    getProduct() {
      serviceProduct.getAllProduct().then((res) => {
        this.products = res.data.data;
        console.log(this.products);
      });
    },
    clickRowProduct(item) {
      console.log(item);
    },
    clickNewProduct() {
      this.getCategory();
      this.addDialog = true;
    },
    clickSave() {
      this.newItem.id_category = this.select.id;
      serviceProduct.postProduct(this.newItem).then((res) => {
        console.log(res);
        this.getProduct()
        this.addDialog = false;

      });
    },
    getCategory() {
      serviceCategory.getAllCategory().then((res) => {
        this.categories = res.data.data;
        this.select = this.categories[0];
      });
    },
  },
};
</script>

